/* src/styles/Main.css */

/* General Styles */
body {
    font-family: 'Arial', sans-serif; /* Fallback to a generic font family */
    color: #333; /* Dark text color for readability */
    background-color: #f5f5f5; /* Light background color */
    margin: 0;
    padding: 0;
    scroll-behavior: smooth; /* Smooth scrolling for anchor links */
}

/* Container */
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

/* Header Styles */
.header {
    background: linear-gradient(to right, #003366, #000080); /* Prominent gradient background */
    color: #fff;
    padding: 20px 0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 1000;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar a {
    color: #fff;
    text-decoration: none;
    font-size: 1.1em;
    margin: 0 15px;
    transition: color 0.3s ease;
}

.navbar a:hover {
    color: #f0f0f0;
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
}

/* Hero Section */
.hero {
    background: url('../Assets/hero-bg.jpg') no-repeat center center/cover; /* Example background image */
    color: #fff;
    text-align: center;
    padding: 100px 20px;
    position: relative;
    background-attachment: fixed; /* Parallax effect */
    background-size: cover;
}

.hero-title {
    font-size: 3em;
    margin-bottom: 20px;
    animation: fadeIn 2s ease-out;
}

.hero-description {
    font-size: 1.2em;
    margin-bottom: 30px;
    animation: fadeIn 3s ease-out;
}

.hero-button {
    background-color: #ffffff; /* White button */
    color: #004080;  /* Dark blue text */
    padding: 10px 20px;
    border: none;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1em;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease; /* Ensure ease is applied */
    display: inline-block; /* Ensure the button behaves as an inline block for scaling */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Added shadow for better visibility */
}

.hero-button:hover {
    background-color: #000080;  /* Dark blue on hover */
    color: #f0f0f0;
    transform: scale(1.1); /* Adjust scale for hover effect */
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Darker shadow on hover */
}

/* About Us Section */
.about {
    background-color: #ffffff; /* White background */
    color: #333;
    padding: 50px 20px;
    text-align: center;
    animation: fadeIn 2s ease-out;
}

.about-title {
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #000080; /* Dark blue */
}

.about-description {
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 30px;
}

.about-img {
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 100%;
    height: auto;
}

/* Footer Styles */
.footer {
    background-color: #000080; /* Dark blue */
    color: #fff;
    padding: 20px;
    text-align: center;
}

.footer a {
    color: #ffffff;
    text-decoration: none;
    transition: color 0.3s ease;
}

.footer a:hover {
    color: #f0f0f0;
    text-decoration: underline;
}

/* Section Zoom Animations */
.section {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 0.6s ease, transform 0.6s ease;
}

.section.in-view {
    opacity: 1;
    transform: scale(1);
}

/* Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
