/* src/styles/Footer.css */

.footer {
    background-color: #004080; /* Dark blue background */
    color: white;
    padding: 20px 0;
    position: relative;
    bottom: 0;
    width: 100%;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 20px;
  }
  
  .footer-left,
  .footer-right {
    flex: 1;
  }
  
  .footer-title {
    font-size: 1.8em;
    margin: 0;
  }
  
  .footer-description {
    font-size: 1.2em;
    margin: 10px 0;
  }
  
  .footer-heading {
    font-size: 1.5em;
    margin-bottom: 10px;
  }
  
  .footer-links {
    list-style: none;
    padding: 0;
  }
  
  .footer-link {
    color: white;
    text-decoration: none;
    margin-bottom: 10px;
    display: block;
    font-size: 1.2em;
  }
  
  .footer-link:hover {
    text-decoration: underline;
  }
  
  .footer-bottom {
    text-align: center;
    padding: 10px 0;
    border-top: 1px solid #ffffff;
  }
  
  .footer-text {
    font-size: 1em;
    margin: 0;
  }
  